import wtsTools from '@/static/js/wtsTools.esm.min.js'
import http from '../api/http'
import config from '../api/common'
import notice from '../api/notice'
const tools=wtsTools.tools;
const weixin={
        isReady:false,
        init(){
            let self=this;
            return new Promise((resolve, reject)=>{
                if (!tools.isWeiXin())return reject();
                if(self.isReady){
                    return resolve();
                }
                var url = location.href.split('#')[0];
                // 
                http.request({
                  url: "/content/jsTicket",
                  type:'get',
                  data:{
                    url:url
                  }
                  }).then(data => {
                        wx.config({
                            debug: false, // ,apialertpclogpc
                            appId: data.data.appId, // 
                            timestamp: data.data.timeStamp, // 
                            nonceStr: data.data.nonStr, // 
                            signature: data.data.signature, // 1
                            jsApiList: ['updateAppMessageShareData', 'updateTimelineShareData','hideMenuItems'] // JSJS2
                        });
                        wx.ready(function () {
                            //
                            self.isReady=true;
                            resolve()
                        });
                        wx.error(function (res) {
                            console.log(res);
                            reject(res)
                        });
                  }).catch(err=>{
                    console.log("");
                  })
            });
        },
        share(title,desc,img,success,fali){
            if (!tools.isWeiXin())return;
            let initData={
                title: title,
                desc: desc,
                link: location.href,
                imgUrl: img,
                success: function () {
                    if(success){
                        success();
                    }
                },
                cancel: function () {
                    notice.msg('');
                    if(fali){
                        fali();
                    }
                },
                fail: function (res) {
                    if(fali){
                        fali();
                    }
                }
            };
            this.init().then(()=>{
                wx.updateAppMessageShareData(initData);
                //
                wx.updateTimelineShareData(initData);
            }).catch((e)=>{
                if(fali){
                    fali(e);
                }
            })
        },
        shareDetail(title,desc,img,success,fali){
            if (!tools.isWeiXin())return;
            let initData={
                title: title,
                desc: desc,
                link: location.href,
                imgUrl: img,
                success: function () {
                    if(success){
                        success();
                    }
                },
                cancel: function () {
                    notice.msg('');
                    if(fali){
                        fali();
                    }
                },
                fail: function (res) {
                    if(fali){
                        fali();
                    }
                }
            };
            this.init().then(()=>{
                wx.updateAppMessageShareData(initData);
                //
                wx.updateTimelineShareData(initData);
                wx.hideMenuItems({
                    menuList: [
                        'menuItem:share:weiboApp', // Weibo
                        'menuItem:favorite', // 
                        'menuItem:share:facebook', // FB
                        'menuItem:share:QZone', //  QQ 
                        'menuItem:editTag', // 
                        'menuItem:delete', // 
                        'menuItem:copyUrl', // 
                        'menuItem:readMode', // :
                        'menuItem:share:email', //
                        'menuItem:openWithQQBrowser', //QQ
                        'menuItem:openWithSafari', //Safari
                        'menuItem:originPage', //
                    ],
                    success: function (res) {
                        console.log('');
                    },
                    fail: function (res) {
                        console.log(JSON.stringify(res));
                    }
                });
            }).catch((e)=>{
                if(fali){
                    fali(e);
                }
            })
        },
        hideWxBtns(type){
            //
            if (!tools.isWeiXin())return
            this.init().then(()=>{
                wx.hideOptionMenu();
            }).catch(()=>{
                console.log('');
            })
        },
        showWxBtns(type){
          //
          if (!tools.isWeiXin())return
          this.init().then(()=>{
              wx.showOptionMenu();
          }).catch(()=>{
              console.log('');
          })
        },
        //
        close(){
            if(tools.isWtsNativeApp()){
                nativeApp.general.deviceready(function(){
                    nativeApp.general.closeWebView();
                })
            }
            if(tools.isWeiXin() && WeixinJSBridge){
                WeixinJSBridge.invoke('closeWindow',{},function(res){
                        
                });
            }
        }
}

export default weixin
