
const http = {
    /**
     * jsajax
     * >>new XMLHttpRequest ,IE(IE6XMLHttpRequest)
     * >>es6,,babeles5 https://babeljs.cn/docs/setup/#installation
     *  @param settings jQuery ajax
     *  ,dataContent-Type
     *  Content-Type                        data                                     
     *  application/x-www-form-urlencoded   'name=&age=12'{name:'',age:12}  ,&
     *  application/json                     name=&age=12'                        json
     *  multipart/form-data                  new FormData()                           FormData,FormData,Content-Type
     *  :.
     */
    ajax: (settings = {}) => {
      // 
      let _s = Object.assign({
        url: '', // string
        type: 'POST', // string 'GET' 'POST' 'DELETE'
        dataType: 'json', // string :'json' 'text' 'document' ...
        async: true, //  boolean true: false: required
        data: null, // any ,dataContent-Type
        contentType: "application/x-www-form-urlencoded", // Content-Type
        headers: {}, // object 
        timeout: 1000, // string :0
        beforeSend: (xhr) => {
        },
        success: (result, status, xhr) => {
        },
        error: (xhr, status, error) => {
        },
        complete: (xhr, status) => {
        }
      }, settings);
      // XMLHttpRequest
      let xhr = new XMLHttpRequest();
      // 
      xhr.addEventListener('loadstart', e => {
        _s.beforeSend(xhr);
      });
      // 
      xhr.addEventListener('load', e => {
        const status = xhr.status;
        if ((status >= 200 && status < 300) || status === 304) {
          let result;
          if (xhr.responseType === 'text') {
            result = xhr.responseText;
          } else if (xhr.responseType === 'document') {
            result = xhr.responseXML;
          } else {
            result = JSON.parse(xhr.response);
          }
          // :200/,
          _s.success(result, status, xhr);
        } else {
          _s.error(xhr, status, e);
        }
      });
      // 
      xhr.addEventListener('loadend', e => {
        _s.complete(xhr, xhr.status);
      });
      // 
      xhr.addEventListener('error', e => {
        _s.error(xhr, xhr.status, e);
      });
      // 
      xhr.addEventListener('timeout', e => {
        _s.error(xhr, 408, e);
      });
      let useUrlParam = false;
      let sType = _s.type.toUpperCase();
      // "",dataurl
      if (sType === 'GET' || sType === 'DELETE') {
        useUrlParam = true;
        _s.url += http.getUrlParam(_s.url, _s.data);
      }
      // 
      xhr.open(_s.type, _s.url, _s.async);
      _s.headers['Content-Type']=_s.contentType;
      _s.headers['X-Requested-With']='XMLHttpRequest';
      // 
      for (const key of Object.keys(_s.headers)) {
        xhr.setRequestHeader(key, _s.headers[key]);
      }
      // 
      if (_s.async && _s.timeout) {
        xhr.timeout = _s.timeout;
      }
      // .,null.,Content-Type
      xhr.send(useUrlParam ? null : http.getQueryData(_s.data));
    },
    // dataurl
    getUrlParam: (url, data) => {
      if (!data) {
        return '';
      }
      let paramsStr = data instanceof Object ? http.getQueryString(data) : data;
      return (url.indexOf('?') !== -1) ? paramsStr : '?' + paramsStr;
    },
    // ajax
    getQueryData: (data) => {
      if (!data) {
        return null;
      }
      if (typeof data === 'string') {
        return data;
      }
      if (data instanceof FormData) {
        return data;
      }
      return http.getQueryString(data);
    },
    // 
    getQueryString: (data) => {
      let paramsArr = [];
      if (data instanceof Object) {
        Object.keys(data).forEach(key => {
          let val = data[key];
          // todo Dateapi
          if (val instanceof Date) {
            // val = dateFormat(val, 'yyyy-MM-dd hh:mm:ss');
          }
          paramsArr.push(encodeURIComponent(key) + '=' + encodeURIComponent(val));
        });
      }
      return paramsArr.join('&');
    }
  }

  export default http